import React from "react";
import postMessageToParent from "./postMessageToParent";
import "./styles.css";

export default function ExpiryDate() {
  const handleChange = (change) => {
    const { value } = change.target;

    postMessageToParent({
      from: "expiry",
      messageType: "validation",
      data: {
        valid: /^\d{2}[-|\/]\d{2}$/.test(value),
      },
    });
  };

  const handleFocus = () => {
    postMessageToParent({
      from: "expiry",
      messageType: "event",
      data: {
        eventName: "onfocus",
        value: true,
      },
    });
  };

  const handleBlur = () => {
    postMessageToParent({
      from: "expiry",
      messageType: "event",
      data: {
        eventName: "onblur",
        value: true,
      },
    });
  };

  return (
    <input
      type="text"
      placeholder="Valid thru"
      name="expiry"
      onChange={handleChange}
      onFocus={handleFocus}
      onBlur={handleBlur}
    />
  );
}
